import { React, useState, useEffect, useContext, useMemo } from 'react'
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import { AdminContext } from '../context/AdminContext';
import ProductServices from '../services/ProductServices';
import { useNavigate } from "react-router-dom";
import { notifySuccess, notifyError } from '../utils/toast';
import Loading from '../components/preloader/Loading';
import { toTitleCase } from '../utils/functions'
import DataTable from '../components/DataTable'

function Products() {

  const { state } = useContext(AdminContext);
  const { adminInfo } = state;
  const [productsList, setProductsList] = useState([])
  const [tempData, setTempData] = useState([])
  const [loading, setLoading] = useState(false);

  const columns = useMemo(
    () => [
      {
        Header: "#",
        accessor: "id",
      },
      {
        Header: " ",
        accessor: " ",
        Cell: ({ value, row }) => {
          return (<><img src={row.original.images[0].src.replace("https", "http")} /></>)
        }
      },
      {
        Header: "Name",
        accessor: "name",
      },
      {
        Header: "SKU",
        accessor: "slug",
      },
      {
        Header: "Type",
        accessor: "type",
        Cell: ({ value, row }) => {
          return toTitleCase(row.original.type)
        }
      }
    ]);

  let navigate = useNavigate();

  function fetchData() {
    setLoading(true)
    ProductServices.getAllProducts({ 'per_page': '100' })
      .then((response) => {
        setProductsList(response)
        setTempData(response)
        setLoading(false)

      })
      .catch((err) => {
        notifyError(err ? err.response.data.message : err.message);
        setLoading(false)

      });
  }

  const onProductSearch = (e) => {
    let t = [];

    if (e.target.value.length == 0) {
      setProductsList(tempData)
    } else {

      tempData.map((product) => {
        if (product.name.toLowerCase().includes(e.target.value.toLowerCase())) {
          t.push(product)
        }
      })
      setProductsList(t)
    }


  }


  const iStyle = {
    marginRight: "10px"
  }

  useEffect(() => {
    //load_data()
  }, []);


  return (
    <>
      <div className="col-lg-12 grid-margin stretch-card">
        <div className="card">
          <div className="card-body">

            <div className="row">
              <div className="col-lg-6">
                <h4 className="card-title">Products</h4>
              </div>

              <div className="col-lg-6">
                <button type="button" className="btn btn-success" style={{ float: "right" }} >
                  <i className="ti-download"></i>
                </button>
                <input type="text" className="form-control-lg" style={{ border: "1px solid #DFDFDF", float: "right", marginRight: "10px" }} placeholder="Search product...." onChange={onProductSearch} />
              </div>
            </div>
            <div className="table-responsive">
              <DataTable
                columns={columns}
                data={productsList}
                fetchData={fetchData}
                loading={loading}
                pageRecords={productsList.length}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default Products