import React, { useState, useEffect } from 'react'
import { useNavigate } from "react-router-dom";
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import useAsync from '../hooks/useAsync';
import useFilter from '../hooks/useFilter';
import OrderServices from '../services/OrderServices';
import ProductServices from '../services/ProductServices';
import { notifySuccess, notifyError } from '../utils/toast';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { Modal, Button } from "react-bootstrap"
import { AdminDetails } from '../utils/functions'
import { getMonth, getYear } from 'date-fns';
import range from "lodash/range";

function CreateOrder() {

    let d = AdminDetails()
    const [tempData, setTempData] = useState([])
    const [startDate, setStartDate] = useState(new Date());
    const [items, setItems] = useState([]);
    const [showToggle, setShowToggle] = useState(false)
    const [productsList, setProductsList] = useState([]);
    const [submiting, setSubmiting] = useState(false)

    function load_data() {
        ProductServices.getAllProducts({ 'per_page': '100' })
            .then((response) => {

                let tData = [];
                response.map((product) => {

                    let dos = 'pm'
                    product.meta_data.map((i) => {

                        if (i.key == "am" && i.value == "1") {
                            dos = "am"
                        }
                    });
                    product.dosage = dos;
                    tData.push(product)

                });
                setProductsList(tData)
                setTempData(tData)
            })
            .catch((err) => {
                notifyError(err ? err.response.data.message : err.message);
            });
    }

    let navigate = useNavigate()
    const addItems = () => {

        if (productsList.length == 0) {
            notifyError('Loading productions please wait...');
            return
        } else {
            setShowToggle(true)
        }

    }

    const closeAddItem = () => {
        setShowToggle(false)
    }

    useEffect(() => {
        load_data()
    }, []);

    const onSelectHandler = (e) => {
        const { name, checked } = e.target;
        let tempProducts = productsList.map((product) =>
            product.id.toString() === name ? { ...product, isChecked: checked, quantity: process.env.REACT_APP_MINIMUM_QUANTITY, dosage: product.dosage } : product
        );
        setProductsList(tempProducts)
    }

    const onChangeHandler = (e) => {
        const { name, value } = e.target;
        let tempProducts = productsList.map((product) =>
            "quantity_" + product.id.toString() === name ? { ...product, quantity: value } : product
        );
        setProductsList(tempProducts)
    }

    const onSelectChangeHandler = (e) => {
        const { name, value } = e.target;
        let tempProducts = productsList.map((product) =>
            "dosage_" + product.id.toString() === name ? { ...product, dosage: value } : product
        );
        setProductsList(tempProducts)
    }

    const onProductSearch = (e) => {
        let t = [];

        if (e.target.value.length == 0) {
            setProductsList(tempData)
        } else {

            tempData.map((product) => {
                console.log(product.name.toLowerCase().includes(e.target.value.toLowerCase()))
                if (product.name.toLowerCase().includes(e.target.value.toLowerCase())) {
                    t.push(product)
                }
            })
            setProductsList(t)
        }


    }

    const {
        register,
        handleSubmit,
        clearErrors,
        setValue,
        formState: { errors },
    } = useForm();

    function goToHome() {
        navigate('/orders')
    }

    const addItemsToSession = () => {

        let tempProducts = [];
        productsList.map((product) => {

            if (product.isChecked) {
                tempProducts.push(product)
            }
        }
        );
        setItems(tempProducts)
        setShowToggle(false)
    }

    const deleteSelectedItem = (id) => {
        let tempProducts = [];

        items.map((item, i) => {
            console.log(item)
            if (id.toString() !== item.id.toString()) {
                tempProducts.push(item)
            }
        })
        setItems(tempProducts)
    }

    const onSubmit = (data) => {

        if (items.length < 3) {
            notifyError('Please add minimum 3 items in the order');
            return
        }

        const itemData = [];
        items.map((item, i) => {
            itemData.push({ 'product_id': item.id, 'quantity': item.quantity })
        })

        const postData = {
            payment_method: 'paytabs_creditcard',
            payment_method_title: 'PayTabs - CreditCard',
            set_paid: false,
            billing: {
                first_name: data.addOrderFirstName,
                last_name: data.addOrderLastName,
                address_1: data.addOrderAddress,
                address_2: '',
                city: data.addOrderEmirates,
                state: '',
                postcode: '',
                country: 'AE',
                email: data.addOrderEmail,
                phone: data.addOrderContactNumber.replace("+", "").replace(" ", "")
            },
            shipping: {
                first_name: data.addOrderFirstName,
                last_name: data.addOrderLastName,
                address_1: data.addOrderAddress,
                address_2: '',
                city: data.addOrderEmirates,
                state: '',
                postcode: '',
                country: 'AE'
            },
            //  customer_id: 424,
            line_items: itemData,
            meta_data: [{
                "key": "partner",
                "value": d.parent_id == '0' ? d.id : d.parent_id
            },
            {
                "key": "sub_partner",
                "value": d.parent_id == '0' ? 0 : d.id
            },
            {
                "key": "sub_partner_name",
                "value": d.name
            },
            {
                "key": "order_source",
                "value": "panel"
            }
            ]
        };
        setSubmiting(true)
        OrderServices.createOrder(postData)
            .then((response) => {
                if (response) {
                    setSubmiting(false)
                    notifySuccess('Action completed successfully!')
                    navigate('/orders')
                }
            })
            .catch((err) => {
                setSubmiting(false)
                notifyError(err ? err.response.data.message : err.message);
            });
    }


    const years = range(1960, getYear(new Date()) + 1, 1);
    const months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
    ];

    return (
        <>
            <form className="form-sample" onSubmit={handleSubmit(onSubmit)}>
                <div className="col-lg-12 grid-margin stretch-card">
                    <div className="card">
                        <div className="card-body">
                            <div className="row mb-3">
                                <div className="col-lg-12">
                                    <h4 className="card-title">Create Order</h4>
                                </div>
                                <div className="col-lg-12">
                                    <button type="button" className="btn btn-danger" onClick={goToHome} style={{ float: "right" }} ><i className="ti-back-left"></i>&nbsp;&nbsp;Cancel</button>
                                    <button type="button" className="btn btn-info" onClick={addItems} style={{ float: "right", marginRight: "10px" }} ><i className="ti-plus"></i>&nbsp;&nbsp;Items</button>
                                    <input type="submit" className="btn btn-primary" style={{ float: "right", marginRight: "10px" }} value={submiting ? "Creating..." : "Create Order"} />
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">First Name <span className="required">*</span></label>
                                        <div className="col-sm-9">
                                            <input type="text" className="form-control form-control-lg" name="addOrderFirstName" {...register("addOrderFirstName", { required: "Please enter first name." })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="addOrderFirstName"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />

                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Last Name <span className="required">*</span></label>
                                        <div className="col-sm-9">
                                            <input type="text" className="form-control form-control-lg" name="addOrderLastName" {...register("addOrderLastName", { required: "Please enter last name." })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="addOrderLastName"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Email <span className="required">*</span></label>
                                        <div className="col-sm-9">
                                            <input type="text" className="form-control form-control-lg" name="addOrderEmail" {...register("addOrderEmail", {
                                                required: "Email is required", pattern: { value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i, message: "Please enter valid email" }
                                            })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="addOrderEmail"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Contact# <span className="required">*</span></label>
                                        <div className="col-sm-9">
                                            <input type="text" className="form-control form-control-lg" placeholder="9715XXXXXXXX" name="addOrderContactNumber" {...register("addOrderContactNumber", { required: "Please enter contact number name." })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="addOrderContactNumber"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Weight (KG)</label>
                                        <div className="col-sm-9">
                                            <input type="number" min="1" max="400" className="form-control form-control-lg" name="addOrderWeight" {...register("addOrderWeight")} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Height</label>
                                        <div className="col-sm-9">
                                            <div className="row">
                                                <div className="col-sm-6">
                                                    <select className="form-control form-control-lg" name="addOrderFeets" {...register("addOrderFeets")}>
                                                        <option>Feets</option>
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                        <option>6</option>
                                                        <option>7</option>
                                                        <option>8</option>
                                                        <option>9</option>
                                                        <option>9+</option>

                                                    </select>
                                                </div>
                                                <div className="col-sm-6">
                                                    <select className="form-control form-control-lg" name="addOrderInches" {...register("addOrderInches")}>
                                                        <option>Inches</option>
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                        <option>6</option>
                                                        <option>7</option>
                                                        <option>8</option>
                                                        <option>9</option>
                                                        <option>10</option>
                                                        <option>11</option>
                                                        <option>12</option>

                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Date of birth <span className="required">*</span></label>
                                        <div className="col-sm-9">
                                            {/*<DatePicker selected={new Date(new Date().setFullYear(new Date().getFullYear() - 18))} onChange={(date) => setStartDate(date)} maxDate={new Date(new Date().setFullYear(new Date().getFullYear() - 18))} name="addOrderDOB" {...register("addOrderDOB")} className="form-control form-control-lg" />*/}
                                            <DatePicker className="form-control form-control-lg" name="addOrderDOB" {...register("addOrderDOB")}
                                                renderCustomHeader={({
                                                    date,
                                                    changeYear,
                                                    changeMonth,
                                                    decreaseMonth,
                                                    increaseMonth,
                                                    prevMonthButtonDisabled,
                                                    nextMonthButtonDisabled,
                                                }) => (
                                                        <div
                                                            style={{
                                                                margin: 10,
                                                                display: "flex",
                                                                justifyContent: "center",
                                                            }}
                                                        >
                                                            <button onClick={decreaseMonth} disabled={prevMonthButtonDisabled}>
                                                                {"<"}
                                                            </button>
                                                            <select
                                                                value={getYear(date)}
                                                                onChange={({ target: { value } }) => changeYear(value)}
                                                            >
                                                                {years.map((option) => (
                                                                    <option key={option} value={option}>
                                                                        {option}
                                                                    </option>
                                                                ))}
                                                            </select>

                                                            <select
                                                                value={months[getMonth(date)]}
                                                                onChange={({ target: { value } }) =>
                                                                    changeMonth(months.indexOf(value))
                                                                }
                                                            >
                                                                {months.map((option) => (
                                                                    <option key={option} value={option}>
                                                                        {option}
                                                                    </option>
                                                                ))}
                                                            </select>

                                                            <button onClick={increaseMonth} disabled={nextMonthButtonDisabled}>
                                                                {">"}
                                                            </button>
                                                        </div>
                                                    )}
                                                selected={startDate}
                                                onChange={(date) => setStartDate(date)}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Gender</label>
                                        <div className="col-sm-9">
                                            <select className="form-control form-control-lg" name="addOrderGender" {...register("addOrderGender")}>
                                                <option>Male</option>
                                                <option>Female</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Address <span className="required">*</span></label>
                                        <div className="col-sm-9">
                                            <input type="text" className="form-control form-control-lg" name="addOrderAddress" {...register("addOrderAddress", { required: "Please enter address." })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="addOrderLastName"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group row">
                                        <label className="col-sm-3 col-form-label">Emirates</label>
                                        <div className="col-sm-9">
                                            <select className="form-control form-control-lg" name="addOrderEmirates" {...register("addOrderEmirates")}>
                                                <option value="abu_dhabi">Abu Dhabi</option>
                                                <option value="dubai">Dubai</option>
                                                <option value="sharjah">Sharjah</option>
                                                <option value="ras_al_khaimah">Ras Al Khaimah</option>
                                                <option value="fujairah">Fujairah</option>
                                                <option value="umm_al_quwaim">Umm Al Quwaim</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {items.length > 0 ? (
                                <div className="row">
                                    <h4>Selected products ({items.length})</h4>
                                    <div className="col-lg-12 grid-margin">
                                        <table className="table table-red">
                                            <tr>
                                                <th width="5%"></th>
                                                <th>Product</th>
                                                <th>SKU</th>
                                                <th>Quantity</th>
                                                <th>Dosage</th>
                                                <th></th>
                                            </tr>
                                            {items.map((item, i) => (
                                                <tr key={i}>
                                                    <td><img src={item.images[0].src.replace("https", "http")} /></td>
                                                    <td>{item.name}</td>
                                                    <td>{item.sku}</td>
                                                    <td>{item.quantity}</td>
                                                    <td>{item.dosage.toUpperCase()}</td>
                                                    <th><button
                                                        type="button"
                                                        className="delte-button"
                                                        onClick={() => deleteSelectedItem(item.id)}><i className="ti-trash"></i> Remove
                                                        </button>
                                                    </th>
                                                </tr>
                                            ))}
                                        </table>
                                    </div>
                                </div>
                            ) : ""}
                        </div>
                    </div>
                </div>
            </form>

            <Modal show={showToggle} size="lg">
                <div className="col-lg-12 grid-margin">
                    <div className="card111">
                        <div className="card-body111">
                            <Modal.Body>
                                <div className="row">
                                    <div className="col-md-12">
                                        <table className="table">
                                            <tr>
                                                <td colSpan="6"><input type="text" name="order_product" id="order_product" placeholder="Search product ...." className="form-control form-control-lg" onChange={onProductSearch} /></td>
                                            </tr>
                                            <tr>
                                                <th width="1%"></th>
                                                <th width="5%"></th>
                                                <th>Product</th>
                                                <th>SKU</th>
                                                <th>Quantity</th>
                                                <th>Dosage</th>
                                            </tr>
                                            <tbody>
                                                {productsList.map((product, i) => (
                                                    <tr key={i}>
                                                        <td>
                                                            <input
                                                                type="checkbox"
                                                                className="form-check-input"
                                                                name={product.id}
                                                                onChange={onSelectHandler}
                                                                style={{ width: "20px", height: "20px" }}
                                                                checked={product.isChecked ? true : false}
                                                            />
                                                        </td>
                                                        <td><img src={product.images[0].src.replace("https", "http")} /></td>
                                                        <td>{product.name}</td>
                                                        <td>{product.sku}</td>
                                                        <td>
                                                            <input
                                                                type="number"
                                                                name={'quantity_' + product.id} disabled={product.isChecked ? false : true} className="form-control"
                                                                value={product.isChecked ? product.quantity : ''}
                                                                onChange={onChangeHandler}
                                                                min="28" step="28"
                                                            /></td>
                                                        <td>
                                                            <select
                                                                className="form-control"
                                                                name={'dosage_' + product.id} disabled={true}
                                                                onChange={onSelectChangeHandler}
                                                            >
                                                                {product.dosage == "am" ? (
                                                                    <option value="am" selected>AM</option>
                                                                ) : (
                                                                        <option value="am">AM</option>
                                                                    )}

                                                                {product.dosage == "pm" ? (
                                                                    <option value="pm" selected>PM</option>
                                                                ) : (
                                                                        <option value="pm">PM</option>
                                                                    )}




                                                            </select></td>
                                                        <td></td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>

                            </Modal.Body>
                            <Modal.Footer>
                                <button
                                    type="button"
                                    className="btn btn-primary"
                                    style={{ float: "right", "marginRight": "10px" }}
                                    value="Add items"
                                    onClick={addItemsToSession}
                                >Add Items</button>
                                <button type="button" className="btn btn-danger" style={{ float: "right", "marginRight": "10px" }} onClick={closeAddItem}>Cancel</button>
                            </Modal.Footer>
                        </div>
                    </div>
                </div>
            </Modal>
        </>
    )
}

export default CreateOrder