import requests from './httpService';

const OrderServices = {

  getAllOrders(body) {
    return requests.post('/panel_orders', body);
  },
  createOrder(body) {
    return requests.post('/orders', body);
  },

  getOrderByUser(id, body) {
    return requests.get(`/orders/user/${id}`, body);
  },

  getOrderById(id) {
    return requests.get(`/orders/${id}`);
  },

  updateOrder(id, body, headers) {
    return requests.put(`/orders/${id}`, body, headers);
  },

  deleteOrder(id) {
    return requests.delete(`/orders/${id}`);
  },
};

export default OrderServices;
