import requests from './httpService';

const DoctorServices = {
  loginDoctor(body) {
    return requests.post(`/doctors/login`, body);
  },
  getAllDoctors(body) {
    return requests.get('/doctors', body);
  },
  createDoctor(body) {
    return requests.post('/doctors/create', body);
  },

  getDoctorById(id, body) {
    return requests.get(`/doctors/${id}/?doctor_id =${id}`, body);
  },

  updateDoctor(id, body) {
    return requests.put(`/doctors/update/${id}`, body);
  },
  getAllSubAccounts(id) {
    return requests.get(`/doctors/accounts/${id}`, {});
  },
  createSubAccount(body) {
    return requests.post('/accounts/create', body);
  },
  updateAccount(id, body) {
    return requests.put(`/accounts/update/${id}`, body);
  },
  deleteAccount(id) {
    return requests.delete(`/accounts/${id}`);
  },
};

export default DoctorServices;
