import { React, useState, useEffect, useMemo } from 'react'
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import { AdminContext, AdminProvider } from '../context/AdminContext';
import useAsync from '../hooks/useAsync';
import useFilter from '../hooks/useFilter';
import OrderServices from '../services/OrderServices';
import { useNavigate } from "react-router-dom";
import Loading from '../components/preloader/Loading';
import { toTitleCase, AdminDetails } from '../utils/functions'
import { notifySuccess, notifyError } from '../utils/toast';
import DataTable from '../components/DataTable'

function Orders() {


  let d = AdminDetails();
  let navigate = useNavigate();

  const [data, setData] = useState([])
  const [tempData, setTempData] = useState([])
  const [loading, setLoading] = useState(false);

  const columns = useMemo(
    () => [
      {
        Header: "Order##",
        accessor: "id",
      },
      {
        Header: "Name",
        accessor: "billing.first_name",
        Cell: ({ value, row }) => {
          return (<>{row.original.billing.first_name.toUpperCase()} {row.original.billing.last_name.toUpperCase()}</>)
        }
      },
      {
        Header: "Email",
        accessor: "billing.email",
      },
      {
        Header: "Total",
        accessor: "total",
        Cell: ({ value, row }) => {
          return (<>{row.original.currency} {row.original.total}</>)
        }
      },
      {
        Header: "City",
        accessor: "created_at",
        Cell: ({ value, row }) => {
          return toTitleCase(row.original.shipping.city.replace("_", " "))
        }
      },
      {
        Header: "Creator",
        accessor: "creator",
      },
      {
        Header: "Status",
        accessor: "status",
        Cell: ({ value, row }) => {
          return <><strong><label className={getStatusColor(row.original.status)}>{row.original.status.toUpperCase()}</label></strong></>
        }
      },
      {
        Header: "",
        accessor: " ",
        Cell: ({ value, row }) => {
          return <><button type="button" className="btn btn-success btn-sm" onClick={() => openOrderDetails(row.original.id)} ><i className="ti-eye"></i></button></>
        }
      }
    ]);

  function fetchData() {
    setLoading(true)
    OrderServices.getAllOrders()
      .then((response) => {
        setData(response)
        setTempData(response)
        setLoading(false);
      })
      .catch((err) => {
        notifyError(err ? err.response.data.message : err.message);
        setLoading(false);
      });
  }

  const createOrder = () => {
    navigate('/orders/create')
  }


  const onSearch = (e) => {
    let t = [];
    console.log(e.target.value)

    if (e.target.value.length == 0) {
      setData(tempData)
    } else {

      tempData.map((order) => {
        if ((order.id == e.target.value) || (order.billing.first_name.toLowerCase().includes(e.target.value.toLowerCase()))) {
          t.push(order)
        }
      })
      setData(t)
    }
  }

  const getStatusColor = (status) => {
    switch (status) {
      case "pending":
        return "btn btn-info btn-sm"
        break;
      case "failed":
        return "btn btn-danger btn-sm"
        break;
      case "payment-complete":
        return "btn btn-warning btn-sm"
        break;
      case "payment-pending":
        return "btn btn-secondary btn-sm"
        break;
      case "completed":
        return "btn btn-success btn-sm"
        break;
      case "cancelled":
        return "btn btn-danger btn-sm"
        break;
      case "on-hold":
        return "btn btn-info btn-sm"
        break;
      case "processing":
        return "btn btn-success btn-sm"
        break;
    }
  }

  const openOrderDetails = (id) => {
    navigate('/orders/' + id)
  }


  const iStyle = {
    marginRight: "10px"
  }

  useEffect(() => {
  }, []);


  return (

    <>
      <div className="col-lg-12 grid-margin stretch-card">
        <div className="card">
          <div className="card-body">

            <div className="row">
              <div className="col-lg-6">
                <h4 className="card-title">Orders</h4>
              </div>

              <div className="col-lg-6">
                <button type="button" className="btn btn-success" style={{ float: "right" }} >
                  <i className="ti-download"></i>
                </button>
                <button type="button" className="btn btn-primary" style={{ float: "right", "marginRight": "10px" }} onClick={createOrder}><i className="ti-plus"></i>&nbsp;Create Order</button>
                <input type="text" className="form-control-lg" style={{ border: "1px solid #DFDFDF", float: "right", marginRight: "10px" }} placeholder="Search order...." onChange={onSearch} />
              </div>
            </div>
            <div className="table-responsive">
              <DataTable
                columns={columns}
                data={data}
                fetchData={fetchData}
                loading={loading}
                pageRecords={data.length}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default Orders