import React, { useState, useEffect } from 'react'
import { useNavigate } from "react-router-dom";
import OrderServices from '../services/OrderServices';
import { useParams } from 'react-router-dom';
import { notifySuccess, notifyError } from '../utils/toast';
import { toTitleCase } from '../utils/functions'
import Loading from '../components/preloader/Loading';


function OrderDetail(props) {
    const [orderData, setOrderData] = useState({})
    const [loading, setLoading] = useState(true)
    const params = useParams();

    useEffect(() => {
        load_data()
    }, []);

    function load_data() {
        //  setLoading(true)
        OrderServices.getOrderById(params.id)
            .then((response) => {
                setOrderData(response)
                setLoading(false);
            })
            .catch((err) => {
                notifyError(err ? err.response.data.message : err.message);
                setLoading(false);
            });
    }

    return (
        <>
            <div className="col-lg-12 grid-margin stretch-card">
                <div className="card">
                    <div className="card-body">
                        <div className="row">
                            <div className="col-lg-12">
                                <h4 className="card-title"> Order #{params.id} Details</h4>
                            </div>
                        </div>
                        {loading ? <Loading loading={loading} /> : (
                            <>

                                <div className="row">
                                    <div className="col-lg-4">
                                        <h5>General</h5>
                                        <p>Order key: {orderData.order_key}</p>
                                        <p>Created at: {orderData.order_created}<br />
                                    Status: {(orderData.status)}
                                        </p>
                                    </div>
                                    <div className="col-lg-4">
                                        <h5>Billing</h5>
                                        <p>{orderData.billing.first_name} {orderData.billing.last_name}
                                            {orderData.billing.address} <br />{orderData.billing.city}</p>
                                        <p><strong>Email: </strong><br />{orderData.billing.email}</p>
                                        <p><strong>Phone: </strong><br />{orderData.billing.phone}</p>
                                    </div>
                                    <div className="col-lg-4">
                                        <h5>Shipping</h5>
                                        <p>{orderData.shipping.first_name} {orderData.shipping.last_name}
                                            {orderData.shipping.address} <br />{orderData.shipping.city}</p>
                                    </div>
                                </div>
                                <div className="row  mt-20">
                                    <div className="col-lg-12 table-responsive">
                                        <table className="table table-striped">
                                            <thead>
                                                <th>Item</th>
                                                <th>Qty</th>
                                                <th>Total ({orderData.currency})</th>
                                                <th>Tax ({orderData.currency})</th>
                                            </thead>
                                            <tbody>
                                                {orderData.line_items.map((item, i) => (
                                                    <tr>
                                                        <td>{item.name}</td>
                                                        <td>{item.quantity}</td>
                                                        <td>{item.total}</td>
                                                        <td>{item.total_tax}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div className="row">
                                    <table className="table" cellpadding="0px" cellSpacing="0px">
                                        <tr>
                                            <td width="90%" className="right-align">Tax: </td>
                                            <td>{orderData.currency} {orderData.total_tax}</td>
                                        </tr>
                                        <tr>
                                            <td className="right-align">Order Total: </td>
                                            <td>{orderData.currency} {orderData.total}</td>
                                        </tr>
                                    </table>
                                </div>
                            </>
                        )}
                    </div>
                </div>
            </div>

        </>
    )
}

export default OrderDetail