import React, { useContext, useState } from 'react';
import logo from '../assets/images/login.png';
import { useNavigate } from "react-router-dom";
import Cookies from 'js-cookie';
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import { AdminContext } from '../context/AdminContext';
import DoctorServices from '../services/DoctorServices';
import { notifySuccess, notifyError } from '../utils/toast';

function Login() {

    const [loading, setLoading] = useState(false);
    const { dispatch } = useContext(AdminContext);
    let navigate = useNavigate();

    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();

    const onSubmit = data => {
        setLoading(true)
        DoctorServices.loginDoctor({ email: data.userName, password: data.userPassword })
            .then((response) => {
                if (response.success) {
                    notifySuccess('Login Success!');
                    dispatch({ type: 'USER_LOGIN', payload: response.data });
                    Cookies.set('adminInfo', JSON.stringify(response.data));
                    setTimeout(() => {
                        navigate('/', { replace: true });
                    }, 1000);

                } else {
                    notifyError('Invalid credentials!');
                    setLoading(false);
                }

            })
            .catch((err) => {
                notifyError(err ? err.response.data.message : err.message);
                setLoading(false);
            });
    }

    return (
        <>
            <div className="container-scroller">
                <div className="container-fluid page-body-wrapper full-page-wrapper">
                    <div className="content-wrapper d-flex align-items-center auth px-0">
                        <div className="row w-100 mx-0">
                            <div className="col-lg-4 mx-auto">
                                <div className="auth-form-light text-left py-5 px-4 px-sm-5">
                                    <div className="brand-logo">
                                        <img src={logo} alt="logo" />
                                    </div>
                                    <h4>Hello! let's get started {process.env.REST_API_BASE_URL}</h4>

                                    <form className="pt-3" onSubmit={handleSubmit(onSubmit)}>
                                        <div className="form-group">
                                            <input type="text" className="form-control form-control-lg" name="userName" placeholder="Username" {...register("userName", { required: "Please enter your user name." })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="userName"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />

                                        </div>
                                        <div className="form-group">
                                            <input type="password" className="form-control form-control-lg" name="userPassword" placeholder="Password" {...register("userPassword", { required: "Please enter password." })} />
                                            <ErrorMessage
                                                errors={errors}
                                                name="userPassword"
                                                render={({ message }) => <p className="form-error">{message}</p>}
                                            />

                                        </div>
                                        <div className="mt-3">
                                            <input type="submit" className="btn btn-block btn-primary btn-lg font-weight-medium auth-form-btn" value={loading ? "Signing in..." : "Sign in"} />
                                        </div>
                                        <div className="my-2 d-flex justify-content-between align-items-center">
                                            <div className="form-check">
                                            </div>
                                                Version 1.0.0
                                            </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>

    )
}
export default Login